/* ---------------------- */
/* Fonts. npm run deploy  */
/* ---------------------- */

@import url("https://fonts.googleapis.com/css2?family=VT323&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;600;700&family=Open+Sans:wght@300&display=swap");

/* ---------------------- */
/* Reset                  */
/* ---------------------- */

*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

img {
  display: block;
  width: 100%;
}

#skill-gh {
  color: white;
  margin-right: 15px;
  height: 30px;
}
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}

/* ---------------------- */
/* Typography             */
/* ---------------------- */

html {
  margin: 0;
  padding: 0;
}

/* ---------------------- */
/* CSS Variables          */
/* ---------------------- */
:root {
  --main-bg-color: #f9f9f9; /* Slighty darker white */
  --main-text-color: #333333; /* Dark Gray for main text */
  --accent-color: #3498db; /* Blue */
  --hover-color: #2980b9; /* Slightly darker blue for hover effects */
  --accent-color2: #04d9ff;
}

/* to push footer to the bottom of page when content is shorter than viewheight port */
.app {
  display: flex;
  flex-direction: column;
  /* justify-content: space-between; */
  min-height: 100vh;
  background-color: var(--main-bg-color);
  font-family: "Open Sans";
  color: var(--main-text-color);
}

/* MAIN */

main {
  display: flex;
  /* justify-content: center; */
  /* align-items: center; */
  padding: 2.25em;
  /* min-height: 100vh; */
}

/* Header */
.nav-bar {
  font-family: Roboto;
  background-color: var(--main-bg-color);
  display: flex;
  height: 80px;
  justify-content: space-between;
  align-items: center;
  padding: 0px 80px 0px;
  position: fixed;
  width: 100%;
  z-index: 10;
  box-shadow: 0 5px 7px -1px rgba(0, 0, 0, 0.2);
}

.nav-bar-dark {
  background-color: var(--main-text-color);
}

.nav-title {
  margin-left: 30px;
}

.nav-menu {
  align-items: center;
  display: flex;
  list-style: none;
}

.nav-menu li {
  padding-left: 30px;
}

.toggle-icon {
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.toggle-img {
  height: 30px;
  width: 30px;
  margin-top: 5px;
}

.nav-menu p {
  border: 1px solid var(--main-bg-color);
  background-color: var(--main-text-color);
  color: var(--main-bg-color);
  border-radius: 10px;
  padding: 8px 20px;
  transition: 0.4s;
  font-size: 16px;
  width: 100px;
  text-align: center;
}

.nav-menu p:hover {
  background-color: var(--hover-color);
  color: var(--main-bg-color);
}

.nav-menu a {
  text-decoration: none;
  color: var(--main-text-color);
  font-family: "Open Sans";
  font-size: 26px;
}

.icon {
  height: 40px;
  color: var(--main-text-color);
}

/* About Section */

.content {
  padding-top: 100px;
  padding-left: 140px;
  padding-right: 140px;
}

.about-section {
  padding-top: 40px;
  padding-bottom: 40px;
  vertical-align: text-top;
  display: flex;
  justify-content: space-between;
  /* height: 400px; */
  margin: 0 10% 0;
}

.description {
  font-family: "Open Sans";
  /* flex: 50%; */
  width: 50%;
  flex-direction: column;
  justify-content: space-around;
}

.main-h1 {
  font-family: VT323;
  color: var(--accent-color);
  font-size: 35px;
  letter-spacing: 1px;
}

.about-name {
  font-family: Roboto;
  color: var(--main-text-color);
  font-size: 70px;
  margin-bottom: 20px;
  letter-spacing: 1px;
}

.bio p {
  display: inline;
  font-size: 25px;
}

.typewriter {
  font-size: 25px;
  color: var(--accent-color);
  display: inline-block;
}

.img-container {
  display: flex;
  /* flex: 50%; */
  justify-content: center;
  align-items: center;
}

.img-container img {
  border-radius: 50%;
  width: 350px;
}

/* Skills Section */

.skills-container {
  margin: 50px 0px;
}
.skills-container h2 {
  margin-bottom: 10px;
}

.languages-container {
  display: flex;
  flex-wrap: wrap;
  gap: clamp(30px, 1vw, 20px);
}

.skill {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.skill img {
  height: 40px;
  width: 40px;
  display: inline;
}

/* Projects Section */

.projects-container {
  grid-gap: 15px;
  display: grid;
  gap: 15px;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  padding: 20px 0 20px;
}

.project-container {
  background: var(--main-bg-color);
  border-radius: 2px;
  box-shadow: 0 10px 20px -15px var(--main-text-color);
  display: flex;
  flex-direction: column;
  position: relative;
}

.proj-header {
  align-items: center;
  background-color: var(--main-text-color);
  display: flex;
  justify-content: space-between;
}

.proj-header h1 {
  font-size: 20px;
  margin: 10px;
  white-space: nowrap;
  color: var(--main-bg-color); /* Text color on header */
}

.proj-header img {
  cursor: pointer;
  width: 30px;
  margin-right: 20px;
}

.proj-body {
  background: var(--main-bg-color);
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  transition: all 0.2s ease;
  padding: 10px;
  color: var(--main-text-color);
}

.proj-footer {
  padding-top: 10px;
  color: var(--accent-color);
}

.site-style {
  color: var(--main-text-color);
}

/* Footer */

.footer {
  text-align: center;
  bottom: 0;
  width: 100%;
  height: 3rem;
  font-size: 20px;
}

/* Hamburger Navbar */
.hamburger {
  display: none;
  cursor: pointer;
}

.bar {
  display: block;
  margin: 5px auto;
  width: 25px;
  height: 3px;
  border-radius: 10px;
  background-color: var(--main-text-color);
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

@media screen and (max-width: 1172px) {
  .toggle-img {
    margin: 0;
    margin-top: -15px;
  }

  .bio {
    display: flex;
    flex-direction: column;
  }
  .img-container {
    margin-bottom: 25px;
    margin-top: 60px;
  }

  .content {
    padding: 0 100px;
  }

  .about-section {
    margin: 0;
    text-align: center;
    flex-direction: column-reverse;
    align-items: center;
  }

  .proj-header h1 {
    font-size: 18px;
  }

  .description h2 {
    font-size: 50px;
  }

  .bio {
    text-align: left;
  }

  .bio p {
    font-size: 18px;
  }

  .typewriter {
    display: inline-block;
    font-size: 18px;
  }

  .hamburger {
    display: block;
    margin-left: auto;
  }

  .hamburger.active .bar:nth-child(2) {
    opacity: 0;
  }

  .hamburger.active .bar:nth-child(1) {
    transform: translateY(8px) rotate(45deg);
  }

  .hamburger.active .bar:nth-child(3) {
    transform: translateY(-8px) rotate(-45deg);
  }

  .link-btns {
    position: fixed;
    left: -100%;
    top: 70px;
    gap: 0;
    flex-direction: column;
    width: 100%;
    height: 375px;
    text-align: center;
    background-color: var(--main-bg-color);
    transition: 0.3s;
    z-index: 10;
  }

  .nav-menu {
    flex-direction: column;
  }

  .nav-btns {
    margin: 0;
    margin-left: 50px;
  }

  .link-btns.active {
    left: 0%;
  }

  .nav-item {
    margin: 16px 0;
    display: block;
    color: var(--main-text-color);
    text-align: center;
  }

  .nav-item a {
    color: var(--main-text-color);
  }

  .link-btns button {
    margin-left: 0;
    margin: 15px 0;
  }

  .nav-item:hover {
    color: rgba(0, 0, 0, 0.7);
  }

  .link-btns button:hover {
    color: #000;
  }
}

@media screen and (max-width: 625px) {
  .content {
    padding: 10px 0px 0px;
  }

  .projects-container {
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    padding: 0 30px;
  }

  #project-header {
    padding: 0 30px;
    margin-bottom: 20px;
  }

  .skills-container {
    padding: 0 30px;
  }
}

@media screen and (max-width: 476px) {
  .proj-header h1 {
    font-size: 14px;
  }
  .projects-container {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  }
}

/* Dark Mode Styling */
.dark-text {
  color: var(--main-bg-color);
}

.dark-nav-icon-size {
  height: 40px;
}

.dark-bg {
  background-color: var(--main-text-color);
}

.proj-header-dark {
  background-color: #1d1a1b;
}

.proj-body-dark {
  background-color: #000;
}

.dark-bar {
  background-color: var(--main-bg-color);
}

.dark-about-mains {
  color: var(--accent-color2);
}
